import React, {useEffect, useState} from "react";
import axios from "axios";
import {CCard, CCardHeader, CCardBody, CRow} from '@coreui/react'
import {useForm} from "react-hook-form";

import WalletForm from "./WalletForm";
import {getCookieValue, ipAddress, tokenName, errorToast, successToast} from "../../../../utils/Common"

const ManageWallet = () => {
  const [agency, setAgency] = useState(null)
  const [agencyDetails, setAgencyDetails] = useState(null)

  async function FetchAgency() {
    try {
      let token = getCookieValue(tokenName);
      let url = ipAddress + "admin/getCompanyName"

      let res = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(res)
      // console.log(res.data.error)

      if (res.status === 200 && !res.data.error) {
        // console.log(res.data.recordset)
        setAgency(res.data.recordset)
      }
    } catch (e) {
      // console.log(e)
      errorToast(e.message)
    }
  }

  useEffect(() => {
    FetchAgency().then()
  }, []);

  function ShowAgencyDetails(e) {
    if (!e.target.value) {
      errorToast('Please Select Agency')
      setAgencyDetails(null)
      return false
    }

    let agencyId = e.target.value
    let agencyDetails = agency.filter(x => x.Id === agencyId)[0]
    // console.log(agencyDetails)
    setAgencyDetails(agencyDetails)
  }

  async function FetchAgencyDetailsById(id) {
    let token = getCookieValue(tokenName);
    let url = ipAddress + "admin/getCompanyDetailById"

    let res = await axios.post(url, {id: id}, {
      headers: {'Authorization': `Bearer ${token}`}
    })
    // console.log(res)

    if (res.status === 200 && !res.data.error) {
      // console.log(res.data.recordset)
      setAgencyDetails(res.data.recordset[0])
    }
  }

  return (
    <>
      {/* Search Agency */}
      <CCard className="mb-4">
        <CCardHeader className="bg-dark text-white">
          <h4 className="mb-0">Search</h4>
        </CCardHeader>
        <CCardBody>
          <div className="row justify-content-center py-3">
            <div className="col-md-6 border border-light rounded p-2 shadow">
              <label htmlFor="agencyName">
                <b>Agency Name : <span className="text-danger">*</span></b>
              </label>
              <select onChange={ShowAgencyDetails} name="agencyName" id="agencyName" className="form-select">
                <option value="">--Please Select --</option>
                {agency && agency.length > 0 && agency.map(x =>
                  <option key={x?.Id} value={x?.Id}>{x?.CompanyName} ({x?.CompanyId})</option>
                )}
              </select>
            </div>
          </div>
        </CCardBody>
      </CCard>

      {agencyDetails && agencyDetails.Id && <>
        {/* Agency Details */}
        <CCard className="mb-4">
          <CCardHeader className="bg-dark text-white">
            <h4 className="mb-0">Agency Details</h4>
          </CCardHeader>
          <CCardBody>
            <div className="row">
              <div className="col-md-4">
                <p>Agency Name :<br/><b>{agencyDetails?.CompanyName}</b></p>
              </div>

              <div className="col-md-4">
                <p>Agency Id :<br/><b>{agencyDetails?.CompanyId}</b></p>
              </div>

              <div className="col-md-4">
                <p>
                  Payment Type :<br/><b>{agencyDetails?.PaymentType === "BOTH" ?
                  "BOTH (Cash and Credit)" : agencyDetails?.PaymentType}</b>
                </p>
              </div>

              <div className="col-md-4">
                <p className="mb-md-0">Total Balance :<br/><b>{agencyDetails?.TotalBalance}</b></p>
              </div>

              <div className="col-md-4">
                <p className="mb-md-0">Credit Limit :<br/><b>{agencyDetails?.TotalCreditLimit}</b></p>
              </div>

              <div className="col-md-4">
                <p className="mb-0">Due Amount :<br/><b>{agencyDetails?.DueAmount}</b></p>
              </div>
            </div>
          </CCardBody>
        </CCard>

        {/* Wallet Details */}
        <WalletForm
          agencyDetails={agencyDetails}
          FetchAgency={FetchAgency}
          FetchAgencyDetailsById={FetchAgencyDetailsById}
        />
      </>}
    </>
  )
}

export default ManageWallet
