import React, {useState} from "react";
import axios from "axios";
import {CCard, CCardBody, CCardHeader} from "@coreui/react";
import {useForm} from "react-hook-form";

import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../../utils/Common";

const WalletForm = ({agencyDetails, FetchAgency, FetchAgencyDetailsById}) => {
  const {
    register,
    handleSubmit,
    formState: {errors},
    reset
  } = useForm()

  const [btnDisabled, setBtnDisabled] = useState(false);

  async function onSubmit(data) {
    data["id"] = agencyDetails.Id
    data["CompanyId"] = agencyDetails.CompanyId
    data["UserName"] = agencyDetails.UserName

    try {
      setBtnDisabled(true)
      let token = getCookieValue(tokenName);

      let url = ipAddress + "admin/"
      if(data["type"] === "0") {
        url += "updateAmountInWallet"
      } else {
        url += "updateAmountInCreditLimit"
      }
      // console.log(url)
      // console.log(data)
      // return false

      let res = await axios.post(url, data, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(res)

      if (res.status === 200) {
        // console.log(res.data)

        if (res.data.error) {
          errorToast(res.data.message)
        } else {
          reset()
          FetchAgency()
          successToast(res.data.message)
          FetchAgencyDetailsById(agencyDetails.Id)
        }
      }
      setBtnDisabled(false)
    } catch (e) {
      errorToast(e.message)
      setBtnDisabled(false)
    }
  }

  return (
    <CCard className="mb-4">
      <CCardHeader className="bg-dark text-white">
        <h4 className="mb-0">Wallet Details</h4>
      </CCardHeader>
      <CCardBody>
        <div className="row justify-content-center py-3">
          <div className="col-md-8 border border-light rounded p-2 shadow">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="row">
                {/* Type */}
                <div className="col-md-6 mb-3">
                  <label htmlFor="type">Type : <span className="text-danger">*</span></label>
                  <select {...register("type", {required: 'This field is required.'})}
                          name="type" id="type" className="form-select">
                    {agencyDetails?.PaymentType === "CASH" ? <option value="0">Deposit</option> : <>
                      <option value="">--Please Select --</option>
                      <option value="0">Deposit</option>
                      <option value="1">Credit Limit</option>
                    </>}
                  </select>

                  {errors?.type && <p className='text-danger'>{errors?.type?.message}</p>}
                </div>

                {/* Action */}
                <div className="col-md-6 mb-3">
                  <label htmlFor="action">Action : <span className="text-danger">*</span></label>
                  <select {...register("action", {required: 'This field is required.'})}
                          name="action" id="action" className="form-select">
                    <option value="">--Please Select --</option>
                    <option value="debit">Debit</option>
                    <option value="credit">Credit</option>
                  </select>

                  {errors?.action && <p className='text-danger'>{errors?.action?.message}</p>}
                </div>

                {/* Amount */}
                <div className="col-md-12 mb-3">
                  <label htmlFor="amount">Amount : <span className="text-danger">*</span></label>
                  <input type="text" name="amount" id="amount" className="form-control" autoComplete="off"
                         {...register("amount", {
                           required: 'This field is required.',
                           pattern: {
                             value: /^[0-9]*$/,
                             message: "Only numbers are allowed."
                           }
                         })}
                  />

                  {errors?.amount && <p className='text-danger'>{errors?.amount?.message}</p>}
                </div>

                {/*<div className="col-md-6 mb-3">*/}
                {/*  <label htmlFor="updateAmount">Updated Amount : <span className="text-danger">*</span></label>*/}
                {/*  <input type="text" name="updateAmount" id="updateAmount" readOnly className="form-control"/>*/}
                {/*</div>*/}

                {/* Remarks */}
                <div className="col-md-12 mb-3">
                  <label htmlFor="remarks">Remarks : <span className="text-danger">*</span></label>
                  <textarea name="remarks" id="remarks" className="form-control"
                            {...register("remarks", {required: 'This field is required.'})}/>

                  {errors?.remarks && <p className='text-danger'>{errors?.remarks?.message}</p>}
                </div>

                <div className="col-md-12">
                  <button disabled={btnDisabled} className="btn btn-primary">Update</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </CCardBody>
    </CCard>
  )
}

export default WalletForm
